/*  Masast ve Genel Tasarm */
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 450px;
  height: 100vh;
  margin: 0 auto;
  border-radius: 16px;
  overflow: hidden;
}

.videoCard {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
  border-radius: 16px;
  overflow: hidden;
}

.videoWrapper {
  width: 100%;
  flex: 1;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.videoFrame {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.heartAnimation {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 6rem;
  color: var(--color-white);
  transform: translate(-50%, -50%);
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.5));
  pointer-events: none;
  z-index: 20;
}

.infoCard {
  width: 100%;
  min-height: 60px;
  max-height: 60px;
  background-color: var(--color-black);
  color: var(--color-white);
  padding: 0.75rem 1rem;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
}

.userInfo {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  width: 100%;
}

.userAvatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  border: 1px solid var(--color-white);
  flex-shrink: 0;
}

.userInfoText {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow: hidden;
}

.userName {
  font-weight: 600;
  font-size: 1rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.description {
  font-size: 0.8rem;
  color: #e0e0e0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.followButton {
  background-color: #ff0000;
  color: var(--color-white);
  border: none;
  border-radius: 6px;
  padding: 0.4rem 0.8rem;
  font-size: 0.8rem;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s;
  flex-shrink: 0;
}

.followButton:hover {
  background-color: #cc0000;
}

.actionButtons {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  padding: 1.5rem;
  background-color: #0d0d0d;
  border-top-right-radius: 16px;
  border-bottom-right-radius: 16px;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  flex-shrink: 0;
}

.iconWrapper {
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  backdrop-filter: blur(3px);
}

.iconWrapper:hover {
  transform: scale(1.1);
}

.iconItem {
  font-size: 1.6rem;
  color: var(--color-white);
}

.likedIcon {
  color: #ff0026;
}

.savedIcon {
  color: #ffffff;
}

.readMore {
  color: #00ffff;
  cursor: pointer;
  font-weight: bold;
  margin-left: 5px;
  transition: color 0.2s;
  user-select: none;
}

.readMore:hover {
  color: #ff0077;
}

.description {
  font-size: 0.9em;
  color: #ccc;
  line-height: 1.4;
  display: inline;
}

.clickableDescription {
  cursor: pointer;
}

.optionsContainer {
  position: relative;
  display: inline-block;
}

.moreIcon {
  cursor: pointer;
  font-size: 1.5rem;
  margin-top: 12px;
}

.postOptionsCardCustom {
  position: absolute;
  top: 0;
  right: 0; /* saa hizaland */
  z-index: 9999;
  min-width: 180px;
  max-width: 220px;
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.25);
  padding: 10px;
  overflow: hidden;
}

/*  Mobil iin (max-width: 1024px) */
@media (max-width: 1024px) {
  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 0;
    box-shadow: none;
    padding-top: 0;
    padding-bottom: 50px;
  }

  .videoCard {
    flex: 1;
    width: 100%;
    border-radius: 0;
  }

  .videoWrapper {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }

  .videoFrame {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  .infoCard {
    padding: 0.5rem 0.75rem;
    min-height: 50px;
    max-height: 50px;
    border-radius: 0;
    margin-bottom: 50px;
  }

  .actionButtons {
    position: fixed;
    top: 0;
    z-index: 1000;
    width: 100%;
    max-width: 450px;
    height: 50px;
    display: flex;
    flex-direction: row !important;
    justify-content: space-between !important;
    align-items: center;
    gap: 0;
    padding: 0 1rem;
    background-color: rgb(0, 0, 0);
    border-radius: 0;
  }

  .iconWrapper {
    width: 36px;
    height: 36px;
    background-color: transparent;
  }

  .iconItem {
    font-size: 1.2rem;
    color: var(--color-white);
  }

  .postOptionsCardCustom {
    right: 10px;
    top: 40px;
    max-width: 160px;
  }
}

/*  Kk mobil cihazlar iin (max-width: 450px) */
@media (max-width: 460px) {
  .container {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 100%;
    height: 100vh;
    overflow: hidden;
    position: relative;
    border-radius: 0;
  }

  /* Action buttons her zaman en stte */
  .actionButtons {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 55px;
    display: flex;
    flex-direction: row; /*  eklendi */
    align-items: center;
    justify-content: space-between;
    padding: 0 0.75rem;
    background: rgba(0, 0, 0, 0.35);
    backdrop-filter: blur(6px);
    z-index: 10;
  }

  /* More butonu sa stte ayr */
  .optionsContainer {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .moreIcon {
    font-size: 1.6rem;
    cursor: pointer;
    color: #fff;
    margin-left: 0.5rem;
    margin-top: 0;
  }

  /* Video ksm */
  .videoCard {
    flex: 1;
    margin-top: 55px; /* actionButtons ykseklii kadar boluk brakr */
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .videoWrapper {
    flex: 1;
    width: 100%;
    overflow: hidden;
  }

  .videoFrame {
    width: 100%;
    height: 100%;
    aspect-ratio: 9 / 16;
    object-fit: cover;
  }

  /* Footer / ierik ksm */
  .infoCard {
    padding: 0.8rem 1rem;
    background: rgba(0, 0, 0, 0.55);
    color: #fff;
    min-height: 80px; /* biraz daha yksek */
  }

  .userAvatar {
    width: 34px;
    height: 34px;
  }

  .userName {
    font-size: 0.9rem;
    font-weight: 600;
  }

  .description {
    font-size: 0.75rem;
    line-height: 1.3;
  }

  .followButton {
    font-size: 0.75rem;
    padding: 0.35rem 0.65rem;
  }

  /* Post options kart mobilde biraz daha kk */
  .postOptionsCardCustom {
    right: 5px;
    top: 55px; /* actionButtonsun altnda */
    min-width: 140px;
    max-width: 160px;
    font-size: 0.8rem;
  }
}


/* Masast page */

/* ==========================================================
   MASAST (DESKTOP) GRNM N YEN STLLER (isMobile: false)
   ========================================================== */

/* Ana Kapsayc: Ykseklii 95% kapsayacak */
.container {
    /* Mevcut container stilinizi koruyun, sadece bu eklemeleri yapn */
    /* Masastnde %95 ykseklii (95vh) hedefleyen bir kapsaycya ihtiya var */
    /* Eer tm sayfa container' bu bileenin stndeyse, bu bileen de 100% ykseklik almaldr. */
    /* Ancak kullanc isteine sadk kalmak iin: */
    min-height: 95vh; 
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0; /* Padding'i sfrlyoruz ki %95 ykseklii doru hesaplansn */
}

/* erik Sarmalayc: Sol ve Sa Kutuyu Yan Yana Tutar */
.contentWrapper {
    width: 100%;
    height: 95vh; /* stenen %95 ykseklik */
    max-width: 1400px; /* Maksimum genilik ile modern grnm */
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 8px 30px rgba(0, 0, 0, 0.7);
    display: flex;
    flex-direction: row; /* Yan yana duran iki yap */
    background-color: #242424;
}

/* SOL KUTU: Video Alan (Esnek Genilik ve Tam Ykseklik) */
.videoBox {
    flex-grow: 1; /* Geri kalan alan kapla */
    height: 100%; 
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #000;
    position: relative; /* Overlay elemanlar iin */
}

/* YouTube Iframe */
/* Mevcut .videoFrame stilini .videoPlayer olarak gncelleyebilir veya ikisini de kullanabilirsiniz */
.videoPlayer {
    width: 100%;
    height: 100%;
    border: none;
}

/* KULLANICI BLGLER OVERLAY */
.infoCardDesktopOverlay {
    position: absolute;
    bottom: 20px;
    left: 20px;
    right: 10px;
    color: white;
    padding: 15px;
    background: linear-gradient(to top, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0)); /* effaflk efekti */
    z-index: 10;
    pointer-events: none; /* ocuk elemanlar hari tklanabilirlii kapat */
}

/* Masast Kullanc Bilgileri Stil Ayar */
.userInfoDesktop {
    display: flex;
    align-items: center;
    gap: 15px;
    pointer-events: all; /* indeki elemanlar tekrar tklanabilir */
}

.userAvatarDesktop {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    object-fit: cover;
    border: 3px solid white;
}
/* Dier .userInfoText, .userName, .description stilleri mobil ile ayn kalabilir */


/* SA KUTU: Kontroller Alan (80px Sabit Genilik) */
.controlsBox {
    width: 80px; /* Sabit genilik: 80px */
    height: 100%;
    flex-shrink: 0; 
    background-color: #303030;
    display: flex;
    flex-direction: column; 
    align-items: center;
    padding: 15px 0;
    box-sizing: border-box;
    border-left: 2px solid #3c3c3c;
    z-index: 20;
}

/* W1 Balk */
.w1Title {
    font-size: 28px;
    font-weight: 900; 
    color: #00bcd4; 
    text-shadow: 0 0 5px rgba(0, 188, 212, 0.5); 
    margin-bottom: 30px;
    text-align: center;
}

/* Mevcut aksiyonlar sarmalayan kutu (Like, Comment, Share) */
.actionControlsDesktop {
    display: flex;
    flex-direction: column;
    gap: 20px; /* Dikey aralk */
    margin-top: 20px;
    margin-bottom: 20px;
    /* ActionControls iindeki butonlar dikey yerleecektir */
}

/* Daha Fazla (Options) Butonu Sarmalayc */
.optionsContainerDesktop {
    position: relative;
    /* PostOptionsCard mens sola almaldr */
}

/* Yukar/Aa Butonlar Sarmalayc (Dikey Ortalanm) */
.navigationControls {
    margin-top: auto; 
    margin-bottom: auto; 
    display: flex;
    flex-direction: column;
    gap: 20px; 
}

/* Navigasyon Butonlar */
.navButton {
    background-color: #444444;
    color: white;
    border: none;
    border-radius: 50%; 
    width: 55px;
    height: 55px;
    cursor: pointer;
    font-size: 24px;
    transition: background-color 0.2s, transform 0.1s, box-shadow 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
}

.navButton:hover {
    background-color: #00bcd4; 
    transform: scale(1.1);
    box-shadow: 0 6px 15px rgba(0, 188, 212, 0.4);
}

.navButton:active {
    transform: scale(0.95);
    background-color: #008ba3;
}

/* Ok simgesi stilizasyonu */
.arrow {
    display: block;
    line-height: 1; 
}