/* src/components/LoadingOverlay/LoadingOverlay.module.css */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7); /* effaf siyah arka plan */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Dier tm ieriklerin stnde grnmesini salar */
}

.spinner {
  width: 50px;
  height: 50px;
  border: 5px solid #f3f3f3; /* Ak renk kenarlk */
  border-top: 5px solid #3498db; /* Mavi renkli dnen ksm */
  border-radius: 50%;
  animation: spin 1s linear infinite; /* Dnme animasyonu */
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}