.vocentraServer {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: var(--color-black);
  overflow: hidden;
  margin-left: 80px;
}

.mainServerContent {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  overflow: hidden;
}

@media (max-width: 1024px) {
  .vocentraServer {
    margin-left: 0;
  }
}
