.notification_container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background-color: var(--color-black);
  width: 100%;
  height: 100vh;
  font-family: var(--font-main);
  box-sizing: border-box;

  overflow-y: auto;   /* dikey scroll alsn */
  overflow-x: hidden; /* yatay scroll kapal */

  /* Scrollbar gizle */
  scrollbar-width: none; /* Firefox iin */
  -ms-overflow-style: none; /* IE ve Edge iin */
}

.notification_container::-webkit-scrollbar {
  display: none; /* Chrome, Safari ve Opera iin */
}