/*
 * PostThumbnail.module.css
 * Bu stiller, Instagram veya TikTok'taki gibi profil gridi iindeki gnderi grsellerini hedefler.
 */

.thumbnail_container {
  width: 100%;
  padding-bottom: 100%; /* Ykseklii genilie eitlemek iin kullanlr */
  position: relative;
  overflow: hidden;
  cursor: pointer;
  background-color: var(--color-background-secondary, #282828); /* Yklenirken veya grsel yokken arka plan */
  transition: transform 0.2s ease-in-out;
  border-radius: 12px;
}

.thumbnail_container:hover {
  transform: scale(0.98); /* Hafif klme efekti ile tklanabilirlii vurgular */
}

.thumbnail_image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* Grselin kab tamamen doldurmasn salar ve en-boy orann korur */
  object-fit: cover;
}

.multiple_indicator {
  position: absolute;
  top: 8px;
  right: 8px;
  /* konun okunabilirlii iin hafif yar saydam arka plan */
  background-color: rgba(0, 0, 0, 0.4); 
  border-radius: 4px;
  padding: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10; /* Dier ieriin zerinde kalmasn salar */
}