:root {
  --bg-dark: #000000;
  --bg-light: #111111;
  --text: #ffffff;
  --muted: #cccccc;
  --accent: #00bfff;
  --green: #00ff99;
  --glow: 0 0 12px var(--accent), 0 0 24px var(--accent);
}

.wrapper {
  background: var(--special-background03);
  padding: 2rem;
  border-radius: 1.5rem;
  box-shadow: 0 0 20px rgba(0, 170, 255, 0.15);
  backdrop-filter: var(--backdrop-filter);
  color: var(--text-white);
  max-width: 960px;
  margin: auto;
  margin-top: 3rem;
  font-family: var(--font-main);
  transition: all 0.3s ease;
}

.heading {
  font-size: 26px;
  font-weight: bold;
  margin-bottom: 8px;
}

.subtext {
  font-size: 14px;
  color: var(--muted);
  margin-bottom: 24px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 20px;
}

/* ---  KART TASARIMI GNCELLEND --- */

.card {
  background: var(--bg-light);
  border: 2px solid transparent;
  border-radius: 16px;
  padding: 16px;
  transition: all 0.3s ease-in-out;
  
  /*  YEN: Flexbox ile dikey dzen */
  display: flex;
  flex-direction: column;
  /* Kart ieriini (balk ve buton) ayrr */
  justify-content: space-between; 
  height: 100%; /* Kartlarn ayn ykseklikte olmasn salar */
}

.card:hover {
  border-color: var(--accent);
}

.card.close {
  border-color: var(--green);
  box-shadow: 0 0 12px var(--green);
}

/*  YEN: Kart bal (avatar + info) iin dikey ve ortal dzen */
.cardHeader {
  display: flex;
  flex-direction: column;
  align-items: center; /* erii yatayda ortala */
  width: 100%;
}

.avatar {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  object-fit: cover;
  /* margin-bottom kaldrld, gap ile ynetilecek */
}

/*  YEN: sim ve kullanc adn saran info kutusu */
.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px; /* sim ve kullanc ad aras boluk */
  margin-top: 12px;
  width: 100%;
  word-wrap: break-word; /* Uzun isimler iin */
}

.name {
  font-size: 16px;
  font-weight: 600; /* Biraz daha belirgin */
  margin: 0; /* margin-bottom kaldrld */
}

/*  YEN: Kullanc ad iin zel stil */
.username {
  font-size: 13px;
  color: var(--muted);
  margin: 0;
}

.action {
  background: transparent;
  color: var(--accent);
  border: 1px solid var(--accent);
  border-radius: 8px;
  padding: 8px 12px;
  font-size: 13px;
  display: flex;
  align-items: center;
  justify-content: center; /* kon ve metni ortala */
  gap: 6px;
  cursor: pointer;
  transition: all 0.3s;
  
  /*  YEN: Butonun tam genilik kaplamas ve st boluk */
  width: 100%;
  margin-top: 16px; /* Balk ile buton aras boluk */
}

.card.close .action {
  color: var(--green);
  border-color: var(--green);
}

.action:hover {
  background: var(--accent);
  color: var(--text);
}

.card.close .action:hover {
  background: var(--green);
  color: #000;
}

/* ---  MOBL TASARIM GNCELLEND --- */

@media (max-width: 768px) {
  .wrapper {
    padding: 20px;
  }

  .grid {
    grid-template-columns: 1fr;
    gap: 16px;
  }

  .card {
    /* Dikey dzenden tekrar stun dzenine (ayn kalr) */
    flex-direction: column;
    align-items: normal; /* Ortalamay kaldr */
    padding: 16px; /* Padding'i eitle */
  }

  /*  GNCELLEND: Mobil cardHeader yatay (flex-row) olur */
  .cardHeader {
    flex-direction: row; /* Avatar ve info yan yana */
    align-items: center;
    margin-bottom: 12px; /* Butonla arasna boluk koyar */
  }

  .avatar {
    width: 50px;
    height: 50px;
    margin-right: 12px; /* Avatar ve info aras boluk */
    /* margin-top: 0; (flex-direction row olunca gerek kalmaz) */
  }

  /*  GNCELLEND: Mobil info sola hizalanr */
  .info {
    align-items: flex-start; /* Metni sola hizala */
    gap: 2px;
    margin-top: 0;
  }

  .name {
    font-size: 15px;
  }

  .username {
    font-size: 12px;
  }
  
  .action {
    font-size: 13px;
    padding: 8px 12px;
    width: 100%;
    margin-top: 0; /* Boluk cardHeader'dan gelir */
  }
}