.notificationSettingsContainer {
  padding: 40px 20px;
  max-width: 1000px;

  margin: 0 auto;
  color: var(--text-white);
  font-family: var(--font-main);
  height: 100%;
  max-height: 100vh;
  box-sizing: border-box;
  overflow-y: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.notificationSettingsContainer::-webkit-scrollbar {
  display: none;
}

.pageHeader {
  text-align: center;
  margin-bottom: 40px;
  padding-bottom: 20px;
}

.pageHeader h1 {
  font-size: 32px;
   color: var(--text-white);
  background: var(--text-title-gradient);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 10px;
  font-weight: 600;
  letter-spacing: 0.5px;
}

.pageHeader p {
  font-size: 16px;
  color: var(--color-grey);
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
}

.settingsSection {
  margin-bottom: 35px;
  background-color: var(--secondary-bg);
  border-radius: 10px;
  padding: 25px 30px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.sectionTitle {
  font-size: 22px;
  font-weight: 600;
  color: var(--text-white);
  margin-bottom: 25px;
  display: flex;
  align-items: center;
  gap: 12px;
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
}

.sectionTitle svg {
  color: var(--color-blue);
  width: 28px;
  height: 28px;
}

.settingRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.03);
}

.settingRow:last-child {
  border-bottom: none;
}

.settingLabel {
  flex-grow: 1;
  margin-right: 20px;
}

.settingLabel h4 {
  font-size: 17px;
  font-weight: 500;
  color: var(--text-white);
  margin: 0 0 4px 0;
}

.settingLabel p {
  font-size: 13px;
  color: var(--color-grey);
  margin: 0;
  line-height: 1.5;
}

.toggleSwitch {
  position: relative;
  display: inline-block;
  width: 44px;
  height: 24px;
}

.toggleSwitch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggleSlider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color-gray888);
  transition: .3s;
  border-radius: 24px;
}

.toggleSlider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 4px;
  bottom: 4px;
  background-color: var(--text-white);
  transition: .3s;
  border-radius: 50%;
}

input:checked + .toggleSlider {
  background-color: var(--color-blue);
}

input:focus + .toggleSlider {
  box-shadow: 0 0 0 3px rgba(0, 170, 255, 0.2);
}

input:checked + .toggleSlider:before {
  transform: translateX(20px);
  background-color: var(--text-white);
}

.selectInput {
  width: 100%;
  padding: 10px 15px;
  border-radius: 6px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  background-color: var(--bg-main);
  color: var(--text-white);
  font-size: 15px;
  outline: none;
  cursor: pointer;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23e0e0e0%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13%205.7L146.2%20204%2018.3%2075.1a17.6%2017.6%200%200%200-25.7%2023.1l132.4%20132.3c3.5%203.5%208%205.7%2012.9%205.7s9.4-2.2%2012.9-5.7L287%2092.2c1.4-1.4%202.7-2.8%203.9-4.3a17.6%2017.6%200%200%200%200-24.5c-.1-.1-.3-.2-.5-.3z%22%2F%3E%3C%2Fsvg%3E");
  background-repeat: no-repeat;
  background-position: right 12px center;
  background-size: 10px;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.selectInput:hover {
  border-color: rgba(255, 255, 255, 0.15);
}

.selectInput:focus {
  border-color: var(--color-blue);
  box-shadow: 0 0 0 2px rgba(0, 170, 255, 0.2);
}

.timeInputGroup {
  display: flex;
  align-items: center;
  gap: 10px;
}

.timeInput {
  flex-grow: 1;
  padding: 10px 15px;
  border-radius: 6px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  background-color: var(--bg-main);
  color: var(--text-white);
  font-size: 15px;
  outline: none;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.timeInput:focus {
  border-color: var(--color-blue);
  box-shadow: 0 0 0 2px rgba(0, 170, 255, 0.2);
}

.infoBox {
  background-color: var(--secondary-bg);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-left: 4px solid var(--color-blue);
  padding: 15px 20px;
  border-radius: 8px;
  font-size: 14px;
  color: var(--color-grey);
  line-height: 1.6;
  margin-top: 20px;
}

.infoBox h4 {
  color: var(--text-white);
  margin-top: 0;
  margin-bottom: 8px;
  font-size: 16px;
}

.floatingSaveIndicator {
  position: fixed;
  bottom: 25px;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--secondary-bg);
  color: var(--text-white);
  padding: 18px 30px;
  border-radius: 10px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  gap: 20px;
  z-index: 1000;
  animation: fadeInSlideUp 0.4s ease-out forwards;
  border: 1px solid rgba(255, 255, 255, 0.05);
}

@keyframes fadeInSlideUp {
  from {
    opacity: 0;
    transform: translateX(-50%) translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }
}

.saveButton, .resetChangesButton {
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.saveButton {
  background-color: var(--color-blue);
  color: var(--text-white);
}

.saveButton:hover {
  background-color: #0a84d0;
}

.resetChangesButton {
  background-color: transparent;
  color: var(--color-grey);
}

.resetChangesButton:hover {
  color: var(--text-white);
  text-decoration: underline;
}

@media (max-width: 768px) {
  .notificationSettingsContainer {
    padding: 25px 15px;
  }

  .pageHeader {
    margin-bottom: 30px;
    padding-bottom: 15px;
  }

  .pageHeader h1 {
    font-size: 26px;
  }

  .pageHeader p {
    font-size: 14px;
  }

  .settingsSection {
    padding: 20px 20px;
    margin-bottom: 30px;
  }

  .sectionTitle {
    font-size: 20px;
    margin-bottom: 20px;
  }

  .sectionTitle svg {
    width: 24px;
    height: 24px;
  }

  .settingRow {
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
    padding: 15px 0;
  }

  .settingLabel {
    margin-right: 0;
  }

  .settingLabel h4 {
    font-size: 16px;
  }

  .settingLabel p {
    font-size: 12px;
  }

  .toggleSwitch {
    align-self: flex-end;
  }

  .timeInputGroup {
    width: 100%;
    flex-direction: column;
    align-items: stretch;
    gap: 8px;
  }

  .timeInput {
    width: 100%;
  }

  .selectInput {
    width: 100%;
  }

  .floatingSaveIndicator {
    width: calc(100% - 30px);
    bottom: 15px;
    padding: 15px 20px;
    flex-direction: column;
    gap: 15px;
  }

  .saveButton, .resetChangesButton {
    width: 100%;
    text-align: center;
  }
}