/* components/ConfirmationModal/ConfirmationModal.module.css */

/* === Overlay ===
   Daha iddial bir odaklanma iin arka plan koyulatrdk.
   Animasyon "klasik" bir fade-in olarak kald. */
.overlay {
  position: fixed;
  inset: 0;
  /* Daha gl bir odak iin biraz daha koyu bir arka plan */
  background-color: rgba(5, 16, 20, 0.7);
  backdrop-filter: blur(4px); /* Modern ve k bir etki */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  animation: fadeIn 0.2s ease-out;
}

/* === Modal ===
   Daha yumuak glgeler, daha geni i boluklar ve
   daha k bir giri animasyonu (aadan kayarak) eklendi. */
.modal {
  background: #ffffff;
  padding: 32px; /* Daha ferah ve premium bir his iin padding artrld */
  border-radius: 16px; /* Daha modern ve yumuak keler */
  /* Daha k, katmanl bir glge */
  box-shadow: 0 20px 45px rgba(0, 0, 0, 0.1), 0 8px 15px rgba(0, 0, 0, 0.07);
  width: 90%;
  max-width: 420px; /* Genilii hafife artrdk */
  text-align: left;
  transform-origin: center;
  /* Eski zoomIn yerine daha profesyonel bir "slide-up" animasyonu */
  animation: modalSlideUp 0.3s cubic-bezier(0.2, 0.9, 0.3, 1);
}

/* === Tipografi ===
   Hiyerariyi netletirmek iin balk daha "iddial" hale getirildi. */
.modal h3 {
  margin-top: 0;
  margin-bottom: 12px;
  font-size: 1.5rem; /* 24px - Daha gl, iddial bir balk */
  font-weight: 700;
  color: #1a202c; /* Koyu, net bir renk */
}

.modal p {
  font-size: 1rem; /* 16px - Okunakl */
  color: #4a5568; /* Siyah yerine daha yumuak, modern bir metin rengi */
  line-height: 1.6;
  margin-bottom: 32px; /* Butonlarla aray atk */
}

/* === Butonlar ===
   Daha net, modern ve "canlya alnabilir" butonlar.
   Aktif ve hover durumlar daha belirgin. */
.buttons {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
}

.buttons button {
  padding: 12px 20px; /* Daha iyi bir tklama alan */
  border: none;
  border-radius: 8px; /* Modern standart */
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  /* Daha yumuak ve kapsaml bir gei */
  transition: all 0.2s ease-in-out;
}

.buttons button:active {
  transform: scale(0.97); /* Daha belirgin bir tklama efekti */
}

/* Klasik "ptal" butonu - dikkat datmaz */
.cancelButton {
  background-color: #f1f5f9; /* Modern ak gri */
  color: #1a202c;
  border: 1px solid #e2e8f0;
}

.cancelButton:hover {
  background-color: #e2e8f0;
  border-color: #cbd5e1;
}

/* Modern ve "varsaylan" onay butonu */
.confirmButton {
  background-color: #29bdeb; /* Modern, gl bir indigo */
  color: #ffffff;
  box-shadow: 0 4px 14px rgba(79, 70, 229, 0.25);
}

.confirmButton:hover {
  background-color: #46c0e5;
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(79, 70, 229, 0.3);
}

/* "ddial" ve net tehlike butonu */
.confirmButtonDanger {
  background-color: #dc2626; /* Canl ve net bir krmz */
  color: #ffffff;
  box-shadow: 0 4px 14px rgba(220, 38, 38, 0.25);
}

.confirmButtonDanger:hover {
  background-color: #b91c1c;
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(220, 38, 38, 0.3);
}

/* === Animasyonlar === */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Yeni, daha k modal giri animasyonu */
@keyframes modalSlideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}