/*  Mobil ve Kk Ekran Ayarlar (Varsaylan olarak kullanlr) */

/* Ana Kapsayc: Mobil cihazlar iin tam ekran kaplar */
.container {
  height: 100vh;
  width: 100%;
  min-height: 500px;
  max-width: 100%;
  padding: 0;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #050505; 
  color: #f0f0f0;
  overflow: hidden; /* Tamay engeller */
}

/* erik Sarmalayc: Mobil'de tam ekran */
.contentWrapper {
  width: 100%;
  height: 100%;
  max-width: 100%; 
  border-radius: 0;
  box-shadow: none;
  display: flex;
  flex-direction: column; 
  background-color: #242424;
  padding: 0;
  gap: 0;
}

/* Video Alan: Mobil'de tm yatay alan kaplar */
.videoBox {
  flex-grow: 1; 
  width: 100%; 
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Video erevesi */
.mediaFrame {
  width: 100%;
  height: 100%;
  object-fit: contain; /* Video'nun boyutunu korur */
  border-radius: 0;
}

/* Kontroller Alan (Mobil'de genelde gizlenir veya farkl konumlanr) */
/* DataDiscover bileeni navigasyon butonlarn kendi iinde tuttuu iin bu alan altta gizlemiyoruz. */
.controlsBox {
  width: 100%; /* Mobil'de genilii kaplar */
  height: auto; /* erie gre ykseklik alr */
  flex-shrink: 0;
  background-color: #070707;
  display: flex;
  flex-direction: row; /* Mobil'de ikonlar yatay srala */
  justify-content: space-around;
  align-items: center;
  padding: 10px 0;
  box-sizing: border-box;
  z-index: 20;
}

/* W1 Balk (Mobil'de gizleyebiliriz veya kltebiliriz) */
.w1Title {
  display: none; /* Mobil'de sidebar logosu gereksiz */
}

/* Yukar/Aa Butonlar Sarmalayc */
.navigationControls {
  display: flex;
  flex-direction: row; /* Mobil'de yan yana */
  gap: 30px; 
}

/* Navigasyon Butonlar */
.navButton {
  background-color: #444444;
  color: white;
  border: none;
  border-radius: 50%; 
  width: 50px;
  height: 50px;
  cursor: pointer;
  font-size: 20px;
  transition: background-color 0.2s, transform 0.1s, box-shadow 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
}

.navButton:hover {
  background-color: #00bcd4;
}

.navButton:active {
  transform: scale(0.95);
}

.navButton:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Hata Yazs */
.errorText {
  color: #ff4d4f;
  text-align: center;
  padding: 20px;
}


/* ==========================================================
   MASAST (DESKTOP) GRNM N STLLER (min-width: 1025px)
   80px Sol Sidebar'a Gre Ayarland.
   ========================================================== */
@media (min-width: 1025px) {
  /*  Ana Kapsayc: Sidebar'n yanna konumlanr ve kalan alan kaplar */
  .container {
    height: 100vh; 
    min-height: 500px;
    /* Sidebar (80px) + Hafif boluk (rnein 20px) brakarak kalan genilik */
    width: calc(100% - 80px); 
    margin-left: 80px; /* Bileeni 80px saa kaydr */
    padding: 0; 
    box-sizing: border-box;
    display: flex;
    justify-content: center; /* indeki contentWrapper' merkezle */
    align-items: center;
    background-color: var(--color-black);
    max-width: none;
  }

.contentWrapper {
  width: 95%;
  height: clamp(70vh, 80vh, 85vh);
  max-width: 1400px;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: row;
  background-color: #242424;
  padding: 0;
  gap: 0;
}
  
  /* Video Alan: Sol kutu (Byk ksm kaplar) */
  .videoBox {
    flex-grow: 1;
    height: 100%;
    background-color: #000;
  }

  /* Video erevesi */
  .mediaFrame {
    object-fit: contain;
    /* Shorts'lar iin 9:16 oran kullanyorsanz: */
    /* object-fit: cover; */ 
    border-radius: 0;
  }

  /*  Kontroller Alan: Sa kutu (80px Sabit Genilik) */
  .controlsBox {
    width: 80px;
    height: 100%;
    flex-shrink: 0;
    background-color: #070707;
    display: flex;
    flex-direction: column; /* Dikey sralama */
    align-items: center;
    padding: 15px 0;
    box-sizing: border-box;
    z-index: 20;
    justify-content: center; /* Navigasyon butonlarn dikeyde ortalar */
  }

  /* W1 Balk (Masastnde gsterilir) */
  .w1Title {
    display: block;
    font-size: 28px;
    font-weight: 900; 
    color: #ffffff;
    text-shadow: 0 0 5px rgb(0, 183, 255);
    margin-bottom: 30px;
    text-align: center;
  }

  /* Yukar/Aa Butonlar Sarmalayc */
  .navigationControls {
    margin-top: auto; 
    margin-bottom: auto; 
    display: flex;
    flex-direction: column; /* Dikey sralama */
    gap: 20px;
    flex-grow: 1; /* Kontrol kutusunda dikey boluu kaplar */
    align-items: center;
    justify-content: center;
  }
  
  /* Navigasyon Butonlar */
  .navButton {
    width: 55px;
    height: 55px;
    font-size: 24px;
    /* Masast hover/active efektleri */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
  }

  .navButton:hover {
    background-color: #00bbff;
    transform: scale(1.1);
    box-shadow: 0 6px 15px rgba(0, 152, 212, 0.768);
  }

  .navButton:active {
    transform: scale(0.95);
    background-color: #008ba3;
  }
}

@media (max-width: 1024px) {
  .controlsBox {
    display: none;
  }
}